// src/api/services/role/roleService.ts

import axiosInstance from "@/api/axios/axiosInstance";

interface Role {
  id: number;
  name: string;
  display_name: string;
  description: string;
  is_system_role: boolean;
  is_active: boolean;
  created_at?: string;
  updated_at?: string;
}

interface GetAllRolesResponse {
  success: boolean;
  message: string;
  data: Role[];
}

export const getAllRoles = async (): Promise<GetAllRolesResponse> => {
  try {
    const response = await axiosInstance.get<GetAllRolesResponse>("/api/roles");

    if (response.status === 200) {
      return response.data;
    }

    throw new Error("Failed to get roles");
  } catch (error: any) {
    console.error("Error getting roles:", error);
    throw error;
  }
};

export const getRoleById = async (id: number): Promise<Role | null> => {
  try {
    const response = await axiosInstance.get<{success: boolean; data: Role}>(`/api/roles/${id}`);

    if (response.status === 200 && response.data.success) {
      return response.data.data;
    }

    throw new Error("Failed to get role");
  } catch (error: any) {
    console.error("Error getting role:", error);
    return null;
  }
};
