import React from "react";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { 
  Mail, 
  Phone, 
  Calendar,
  UserCheck,
  UserX,
  Crown,
  Shield,
  User,
  Clock
} from "lucide-react";
import { formatDistanceToNow, format } from "date-fns";

interface User {
  id: number;
  username: string;
  email: string;
  first_name: string;
  last_name: string;
  phone_number?: string;
  role_id: string;
  is_active: boolean;
  is_super_admin: boolean;
  created_at: string;
  updated_at: string;
  last_login?: string;
  roles?: Array<{
    id: number;
    name: string;
    display_name: string;
  }>;
}

interface ViewUserDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  user: User | null;
}

const ViewUserDialog: React.FC<ViewUserDialogProps> = ({
  open,
  onOpenChange,
  user,
}) => {
  if (!user) return null;

  const getRoleDisplayName = (user: User) => {
    if (user.roles && user.roles.length > 0) {
      return user.roles.map(role => role.display_name).join(', ');
    }
    if (user.is_super_admin) {
      return "Super Administrator";
    }
    return "No Role";
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[500px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>User Details</DialogTitle>
          <DialogDescription>
            View detailed information about this user account.
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-6">
          {/* User Avatar and Basic Info */}
          <div className="flex items-center space-x-4 p-4 bg-gray-50 rounded-lg">
            <Avatar className="h-16 w-16">
              <AvatarImage src={`https://api.dicebear.com/7.x/initials/svg?seed=${user.first_name} ${user.last_name}`} />
              <AvatarFallback className="text-lg">
                {user.first_name.charAt(0)}{user.last_name.charAt(0)}
              </AvatarFallback>
            </Avatar>
            
            <div className="flex-1">
              <div className="flex items-center gap-2 mb-2">
                <h3 className="text-xl font-semibold">
                  {user.first_name} {user.last_name}
                </h3>
                {/* {user.is_super_admin && (
                  <Crown className="h-5 w-5 text-yellow-500" title="Super Admin" />
                )} */}
              </div>
              
              <div className="flex items-center gap-2">
                <Badge variant={user.is_active ? "default" : "secondary"}>
                  {user.is_active ? (
                    <><UserCheck className="h-3 w-3 mr-1" />Active</>
                  ) : (
                    <><UserX className="h-3 w-3 mr-1" />Inactive</>
                  )}
                </Badge>
                <Badge variant="outline">
                  <Shield className="h-3 w-3 mr-1" />
                  {getRoleDisplayName(user)}
                </Badge>
              </div>
            </div>
          </div>

          {/* Contact Information */}
          <div className="space-y-4">
            <h4 className="text-lg font-medium flex items-center gap-2">
              <User className="h-5 w-5" />
              Contact Information
            </h4>
            
            <div className="grid gap-3">
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <Mail className="h-4 w-4 text-gray-500" />
                <div>
                  <p className="text-sm font-medium text-gray-700">Email</p>
                  <p className="text-sm text-gray-900">{user.email}</p>
                </div>
              </div>
              
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <User className="h-4 w-4 text-gray-500" />
                <div>
                  <p className="text-sm font-medium text-gray-700">Username</p>
                  <p className="text-sm text-gray-900">{user.username}</p>
                </div>
              </div>
              
              {user.phone_number && (
                <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                  <Phone className="h-4 w-4 text-gray-500" />
                  <div>
                    <p className="text-sm font-medium text-gray-700">Phone Number</p>
                    <p className="text-sm text-gray-900">{user.phone_number}</p>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* Account Information */}
          <div className="space-y-4">
            <h4 className="text-lg font-medium flex items-center gap-2">
              <Clock className="h-5 w-5" />
              Account Information
            </h4>
            
            <div className="grid gap-3">
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <Calendar className="h-4 w-4 text-gray-500" />
                <div>
                  <p className="text-sm font-medium text-gray-700">Member Since</p>
                  <p className="text-sm text-gray-900">
                    {format(new Date(user.created_at), 'PPP')} 
                    <span className="text-gray-500 ml-1">
                      ({formatDistanceToNow(new Date(user.created_at), { addSuffix: true })})
                    </span>
                  </p>
                </div>
              </div>
              
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <Calendar className="h-4 w-4 text-gray-500" />
                <div>
                  <p className="text-sm font-medium text-gray-700">Last Updated</p>
                  <p className="text-sm text-gray-900">
                    {format(new Date(user.updated_at), 'PPP')}
                    <span className="text-gray-500 ml-1">
                      ({formatDistanceToNow(new Date(user.updated_at), { addSuffix: true })})
                    </span>
                  </p>
                </div>
              </div>
              
              {user.last_login && (
                <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                  <Clock className="h-4 w-4 text-gray-500" />
                  <div>
                    <p className="text-sm font-medium text-gray-700">Last Login</p>
                    <p className="text-sm text-gray-900">
                      {format(new Date(user.last_login), 'PPP')}
                      <span className="text-gray-500 ml-1">
                        ({formatDistanceToNow(new Date(user.last_login), { addSuffix: true })})
                      </span>
                    </p>
                  </div>
                </div>
              )}
            </div>
          </div>

          {/* System Information */}
          <div className="space-y-4">
            <h4 className="text-lg font-medium flex items-center gap-2">
              <Shield className="h-5 w-5" />
              System Information
            </h4>
            
            <div className="grid gap-3">
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <div className="flex-1">
                  <p className="text-sm font-medium text-gray-700">User ID</p>
                  <p className="text-sm text-gray-900">#{user.id}</p>
                </div>
              </div>
              
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <div className="flex-1">
                  <p className="text-sm font-medium text-gray-700">Account Status</p>
                  <p className="text-sm text-gray-900">
                    {user.is_active ? "Active - Can login and access system" : "Inactive - Cannot login to system"}
                  </p>
                </div>
              </div>
              
              <div className="flex items-center gap-3 p-3 bg-gray-50 rounded-lg">
                <div className="flex-1">
                  <p className="text-sm font-medium text-gray-700">Admin Privileges</p>
                  <p className="text-sm text-gray-900">
                    {user.is_super_admin ? "Super Administrator - Full system access" : "Regular User - Role-based access"}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default ViewUserDialog;
