import React, { useState, useEffect } from "react";
import Page from "@/components/Page";
import Header from "@/components/Header";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Skeleton } from "@/components/ui/skeleton";
import { 
  Users, 
  Eye, 
  Edit, 
  Trash2, 
  Plus, 
  Shield, 
  Mail, 
  Phone, 
  Calendar,
  UserCheck,
  UserX,
  Crown
} from "lucide-react";
import { getAllUsersWithRoles } from "@/api/services/user/userService";
import { getAllRoles } from "@/api/services/role/roleService";
import { toast } from "sonner";
import { formatDistanceToNow } from "date-fns";
import PermissionRoute from "@/components/permissions/PermissionRoute";
// Removed Loading import - using skeleton loading instead
import AddUserDialog from "@/components/Users/AddUserDialog";
import EditUserDialog from "@/components/Users/EditUserDialog";
import DeleteUserDialog from "@/components/Users/DeleteUserDialog";
import ViewUserDialog from "@/components/Users/ViewUserDialog";

interface LocalUser {
  id: number;
  username: string;
  email: string;
  first_name: string;
  last_name: string;
  phone_number?: string;
  role_id: string;
  is_active: boolean;
  is_super_admin: boolean;
  created_at: string;
  updated_at: string;
  last_login?: string;
  role_ids?: string;
  role_names?: string;
  role_display_names?: string;
  roles?: Array<{
    id: number;
    name: string;
    display_name: string;
  }>;
}

interface Role {
  id: number;
  name: string;
  display_name: string;
  description: string;
  is_system_role: boolean;
  is_active: boolean;
}

const UserManagement: React.FC = () => {
  const [users, setUsers] = useState<LocalUser[]>([]);
  const [roles, setRoles] = useState<Role[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedUser, setSelectedUser] = useState<LocalUser | null>(null);
  const [isViewDialogOpen, setIsViewDialogOpen] = useState(false);
  const [isAddUserDialogOpen, setIsAddUserDialogOpen] = useState(false);
  const [isEditUserDialogOpen, setIsEditUserDialogOpen] = useState(false);
  const [isDeleteUserDialogOpen, setIsDeleteUserDialogOpen] = useState(false);
  const [isViewUserDialogOpen, setIsViewUserDialogOpen] = useState(false);

  useEffect(() => {
    fetchUsers();
    fetchRoles();
  }, []);

  const fetchUsers = async () => {
    try {
      setLoading(true);
      const response = await getAllUsersWithRoles();
      if (response.success) {
        // Convert API response to LocalUser format, ensuring username is not undefined
        const usersWithUsername = response.data.map((user: any) => ({
          ...user,
          username: user.username || user.email || `user_${user.id}`
        }));
        setUsers(usersWithUsername);
      } else {
        toast.error("Failed to fetch users");
      }
    } catch (error) {
      console.error("Error fetching users:", error);
      toast.error("Failed to fetch users");
    } finally {
      setLoading(false);
    }
  };

  const fetchRoles = async () => {
    try {
      const response = await getAllRoles();
      if (response.success) {
        setRoles(response.data);
      }
    } catch (error) {
      console.error("Error fetching roles:", error);
    }
  };

  const filteredUsers = (users || []).filter(user =>
    user.username.toLowerCase().includes(searchQuery.toLowerCase()) ||
    user.email.toLowerCase().includes(searchQuery.toLowerCase()) ||
    user.first_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    user.last_name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const getRoleDisplayName = (user: LocalUser) => {
    // First check if user has roles array (new format)
    if (user.roles && user.roles.length > 0) {
      return user.roles.map((role: { id: number; name: string; display_name: string }) => role.display_name).join(', ');
    }

    // Fallback to legacy role_names format
    if (user.role_names) {
      const roleArray = user.role_names.split(',');
      return roleArray.map((roleName: string) => {
        const role = (roles || []).find(r => r.name === roleName.trim());
        return role ? role.display_name : roleName;
      }).join(', ');
    }

    // If user is super admin but no role found, show Super Administrator
    if (user.is_super_admin) {
      return "Super Administrator";
    }

    return "No Role";
  };

  const handleViewUser = (user: LocalUser) => {
    setSelectedUser(user);
    setIsViewUserDialogOpen(true);
  };

  const handleEditUser = (user: LocalUser) => {
    setSelectedUser(user);
    setIsEditUserDialogOpen(true);
  };

  const handleDeleteUser = (user: LocalUser) => {
    setSelectedUser(user);
    setIsDeleteUserDialogOpen(true);
  };

  const handleAddUser = () => {
    setIsAddUserDialogOpen(true);
  };

  const handleUserCreated = () => {
    fetchUsers();
  };

  const handleUserUpdated = () => {
    fetchUsers();
  };

  const handleUserDeleted = () => {
    fetchUsers();
  };

  // Removed loading screen for better UX

  return (
    <PermissionRoute requireSuperAdmin>
      <Page>
        <Header
          title="User Management"
          buttonText="Add User"
          action={handleAddUser}
          filterByName={true}
          filterWord={searchQuery}
          onFilterChange={setSearchQuery}
          useSheet={false}
          showViewToggle={false}
          showFilters={false}
        />

        <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between mb-6 gap-3">
          <div className="flex items-center space-x-2">
            <Badge variant="secondary" className="px-3 py-1">
              <Users className="h-3 w-3 mr-1" />
              <span>{filteredUsers.length} Users</span>
            </Badge>
          </div>
        </div>

        <div className="grid gap-4">
          {loading && users.length === 0 ? (
            // Show skeleton loading for initial load
            Array.from({ length: 3 }).map((_, index) => (
              <Card key={`skeleton-${index}`} className="hover:shadow-md transition-shadow">
                <CardContent className="p-6">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4">
                      <Skeleton className="h-12 w-12 rounded-full" />
                      <div className="flex-1">
                        <Skeleton className="h-5 w-48 mb-2" />
                        <Skeleton className="h-4 w-32 mb-1" />
                        <Skeleton className="h-4 w-24" />
                      </div>
                    </div>
                    <div className="flex items-center space-x-2">
                      <Skeleton className="h-8 w-16" />
                      <Skeleton className="h-8 w-8" />
                      <Skeleton className="h-8 w-8" />
                      <Skeleton className="h-8 w-8" />
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))
          ) : (
            filteredUsers.map((user) => (
            <Card key={user.id} className="hover:shadow-md transition-shadow">
              <CardContent className="p-6">
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-4">
                    <Avatar className="h-12 w-12">
                      <AvatarImage src={`https://api.dicebear.com/7.x/initials/svg?seed=${user.first_name} ${user.last_name}`} />
                      <AvatarFallback>
                        {user.first_name.charAt(0)}{user.last_name.charAt(0)}
                      </AvatarFallback>
                    </Avatar>
                    
                    <div className="flex-1">
                      <div className="flex items-center gap-2">
                        <h3 className="font-semibold text-lg">
                          {user.first_name} {user.last_name}
                        </h3>
                        {/* {user.is_super_admin && (
                          <Crown className="h-4 w-4 text-yellow-500" />
                        )} */}
                        <Badge variant={user.is_active ? "default" : "secondary"}>
                          {user.is_active ? (
                            <><UserCheck className="h-3 w-3 mr-1" />Active</>
                          ) : (
                            <><UserX className="h-3 w-3 mr-1" />Inactive</>
                          )}
                        </Badge>
                      </div>
                      
                      <div className="text-sm text-muted-foreground space-y-1 mt-1">
                        <div className="flex items-center gap-4">
                          <span className="flex items-center gap-1">
                            <Mail className="h-3 w-3" />
                            {user.email}
                          </span>
                          {user.phone_number && (
                            <span className="flex items-center gap-1">
                              <Phone className="h-3 w-3" />
                              {user.phone_number}
                            </span>
                          )}
                        </div>
                        
                        <div className="flex items-center gap-4">
                          <span className="flex items-center gap-1">
                            <Shield className="h-3 w-3" />
                            {getRoleDisplayName(user)}
                          </span>
                          <span className="flex items-center gap-1">
                            <Calendar className="h-3 w-3" />
                            Joined {formatDistanceToNow(new Date(user.created_at), { addSuffix: true })}
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="flex items-center space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleViewUser(user)}
                      className="flex items-center gap-1"
                    >
                      <Eye className="h-4 w-4" />
                      View
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleEditUser(user)}
                      className="flex items-center gap-1"
                    >
                      <Edit className="h-4 w-4" />
                      Edit
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleDeleteUser(user)}
                      className="flex items-center gap-1 text-red-600 hover:text-red-700"
                    >
                      <Trash2 className="h-4 w-4" />
                      Delete
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))
          )}
        </div>

        {filteredUsers.length === 0 && !loading && (
          <div className="text-center py-12">
            <Users className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
            <h3 className="text-lg font-semibold mb-2">No users found</h3>
            <p className="text-muted-foreground mb-4">
              {searchQuery ? "No users match your search criteria." : "No users have been created yet."}
            </p>
            <Button onClick={handleAddUser}>
              <Plus className="h-4 w-4 mr-2" />
              Add First User
            </Button>
          </div>
        )}

        <AddUserDialog
          open={isAddUserDialogOpen}
          onOpenChange={setIsAddUserDialogOpen}
          onUserCreated={handleUserCreated}
        />

        <EditUserDialog
          open={isEditUserDialogOpen}
          onOpenChange={setIsEditUserDialogOpen}
          onUserUpdated={handleUserUpdated}
          user={selectedUser as any}
        />

        <DeleteUserDialog
          open={isDeleteUserDialogOpen}
          onOpenChange={setIsDeleteUserDialogOpen}
          onUserDeleted={handleUserDeleted}
          user={selectedUser as any}
        />

        <ViewUserDialog
          open={isViewUserDialogOpen}
          onOpenChange={setIsViewUserDialogOpen}
          user={selectedUser as any}
        />
      </Page>
    </PermissionRoute>
  );
};

export default UserManagement;
